<template>
    <v-container fluid fill-height class="rand_img" app>
        <v-layout align-center justify-center>
            <v-flex xs12 sm8 md4>
                <v-card class="elevation-12">
                    <v-toolbar dark color="primary">
                        <v-toolbar-title>Entrar no SistemaX</v-toolbar-title>
                        <v-spacer></v-spacer>
                    </v-toolbar>
                    <v-alert v-model="message.alert" :type="message.type" dismissible>
                        {{ message.text }}
                    </v-alert>
                    <v-form @submit.prevent="onLogin" autocomplete="off">

                        <v-card-text>
                            <v-text-field prepend-icon="mdi-account-question" name="login" label="E-mail" type="text"
                                v-model="user.username" required autofocus>
                            </v-text-field>
                            <v-text-field id="password" prepend-icon="mdi-lock-question" name="password"
                                label="Password" type="password" v-model="user.password" required>
                            </v-text-field>

                        </v-card-text>
                        <v-card-actions>
                            <v-btn text @click="onResetPassword">Esqueci a senha</v-btn>
                            <v-spacer></v-spacer>
                            <v-btn :loading="loading" :disabled="loading" color="primary" type="submit"> Entrar <span
                                    slot="loader">Aguarde...</span>
                            </v-btn>

                        </v-card-actions>

                    </v-form>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>
<script>
import firebase from "firebase";

export default {
    name: "login",
    data() {
        return {
            drawer: null,
            loading: false,
            message: {
                text: "Aguarde",
                alert: false,

                type: "error",
            },
            redirect: {},
            user: {
                username: null,
                password: null
            }
        };
    },
    props: {
        source: String
    },
    methods: {
        onLogin: function () {
            let t = this;
            t.loading = true;
            t.message.alert = false;
            if (!this.user.username || !this.user.password) {
                t.loading = false;
                t.message.alert = true;
                t.message.text = "Informe o e-mail e a senha";
                return;
            }
            firebase
                .auth()
                .signInWithEmailAndPassword(this.user.username, this.user.password)
                .then(() => {
                    this.$router.replace({ name: "Dashboard" });
                    t.loading = false;
                })
                .catch(err => {

                    t.loading = false;
                    t.message.alert = true;
                    t.message.text = "Usu치rio e/ou senha inv치lidos";
                    console.log("Usu치rio e/ou senha inv치lidos." + err);

                });

        },

        onResetPassword: function () {
            let t = this;
            t.loading = true;
            t.message.alert = false;
            if (this.user.username == null) {
                t.loading = false;
                t.message.alert = true;
                t.message.text = "Informe o e-mail";
                return;
            }
            firebase
                .auth()
                .sendPasswordResetEmail(this.user.username)
                .then(() => {
                    t.loading = false;
                    t.message.alert = true;
                    t.message.type = "success";
                    t.message.text = "E-mail enviado com sucesso";
                })
                .catch(err => {
                    t.loading = false;
                    t.message.alert = true;
                    t.message.text = "Erro ao enviar e-mail";
                    console.log("Erro ao enviar e-mail." + err);
                });
        }
    }

};
</script>
